.bike-contact {

  &__grid-container{

    @media (min-width: 744px) {
      display: grid;
      grid-auto-flow: column;
    }
  }

  &__title {
    text-align: center;
    margin-bottom: 48px;
  }

  &__table-container {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  &__input {
    margin: 0 auto;
    background: $input-color;
    border: none;
    outline: none;
    width: calc(#{$contact-width} - 12px);
    height: 48px;
    border-radius: 40px;
    padding-left: 12px;
    @extend %text__input;

    @media (min-width: 1020px) {
      width: $contact-width-laptop;
    }

    &--textarea {
      border-radius: 16px;
      padding-bottom: 99px;
      text-align: left;
    }

    &::placeholder {
      font-family: "Poppins", sans-serif;
      font-size: 14px;
    }
  }

  &__button {

    @extend %button;

    @media (min-width: 1020px) {
      width: $contact-width-laptop;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    gap: 8px;

    &-container {
      display: flex;
      flex-direction: column;
      gap: 24px;

      width: 280px;
      margin: 0 auto;
      padding-top: 48px;

      @media (min-width: 744px) {
        margin: 0;
        width: 100%;
        padding-top: 0;
      }
    }
  }
}
