.bike-goods {
  &__title {
    text-align: center;
  }

  &__container {
    display: grid;
    justify-content: center;
  }

  &__card {
    padding-top: 48px;
    width: 280px;
    @media (min-width: 744px){
      width: auto;
    }
  }

  &__img {
    height: 228px;
    width: 280px;

    @media (min-width: 744px){
      height: 316px;
      width: 392px;
    }

    @media (min-width: 1020px){
      height: 264px;
      width: 324px;
    }
    background-size: cover;
  }

  &__model {
    padding-top: 32px;
  }

  &__label {
    padding-top: 8px;
  }

  &__price {
    padding-top: 16px;
  }
}

.fix-price {
  @media (min-width: 1020px) {
    padding-top: 38px;
  }
}
