.container {
  max-width: 2000px;
  padding: 0 20px;
  height: 100vh;

  @media (min-width: 744px) {
    padding-top: 20px;
    margin-left: 72px;
    margin-right: 72px;
  }

  @media (min-width: 1260px) {
    padding-top: 10px;
    margin-left: 120px;
    margin-right: 120px;
  }
}

.header {
  background-image: url(../images/background-photo/header-pic.png);
  background-position: center;
  background-size: cover;

  @media (min-width: 744px) {
    background-image: url(../images/background-photo/header-pic-tablet.png);
  }

  @media (min-width: 1260px) {
    background-image: url(../images/background-photo/header-pic-laptop.png);
  }

  &__content{
    display: grid;
    align-content: space-between;
    height: 480px;

    @media (min-width: 744px) {
      height: 628px;
    }

    @media (min-width: 1260px) {
      height: 711px;
    }
  }

  &__logo {
    background-image: url(../images/logo/logo.png);
    height: 38px;
    width: 128px;
    @media (min-width: 744px) {
      background-image: url(../images/logo/logo-laptop.png);
      height: 56px;
      width: 187px;
    }
  }

  &__num {
    display: none;

    @media (min-width: 744px) {
      display: inline;
      color: $text-color;
      font-family: Poppins;
      font-size: 12px;
      font-weight: 700;
      line-height: 16px;
      letter-spacing: 2px;
      text-align: right;
      margin-right: 20px;

      transform: translateY(-900%);
      transition: transform 0.3s;
    }
  }

  &__call {
    background-image: url(../images/icon/phone.svg);
    background-size: cover;
    width: 18px;
    height: 18px;
    order: 1;


    &:hover + .header__num{
      transform: translateY(0);
  }

  }

  &__burger-menu {
    display: block;
    background-image: url(../images/icon/menu.svg);
    background-size: cover;
    width: 18px;
    height: 12px;
    order: 2;

    &:hover {
      background-image: url(../images/icon/menu_hover.svg);
    }
  }

  &__title {
    margin: 0 auto;
    color: $text-color;
    font-size: 32px;
    line-height: 36px;
    padding-bottom: 88px;

    @media (min-width: 744px) {
      padding-bottom: 130px;
      font-size: 64px;
      line-height: 64px;
    }

    @media (min-width: 1260px) {
      padding-bottom: 203px;
      font-size: 80px;
      line-height: 80px;
    }
  }

  &__action {
    padding-top: 34px;
    display: grid;
    align-items: center;
    grid-template-columns: 1fr 50px 20px;

    @media (min-width: 744px) {
      grid-template-columns: 1fr 1fr 50px 20px;
    }
  }
}
