
.nav {
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  border: 0;
  right: 0;
  background-color: $nav-close-color;

  transform: translateY(-200%);
  transition: transform 0.3s;

  &:target {
    transform: translateY(0);
  }

  @media (max-height: 435px) {
    overflow: auto;
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 32px;

    &:first-child {
      margin-top: 44px;
    }
  }

  &__item {
    display: inline-block;
    position: relative;
    color: $background-color;
    text-decoration: none;
    font-size: 22px;
    line-height: 22px;
    font-weight: 500;
    width: 172px;

    box-shadow: inset 0 0 0 0 $background-color;
    margin: 0 -.25rem;
    padding: 0 .25rem;
    transition: color .3s ease-in-out, box-shadow .3s ease-in-out;

      &:hover {
        box-shadow: inset 200px 0 0 0 $background-color;
        color: $nav-close-color;
      }
  }

  &__logo {
    background-image: url(../images/logo/nav_logo.png);
    background-size: cover;
    height: 38px;
    width: 130px;

    @media (min-width: 1020px) {
      height: 56px;
      width: 187px;
    }
  }

  &__cross {
    background-image: url(../images/icon/cross.svg);
    background-size: cover;
    height: 16px;
    width: 16px;
  }

  &__action{
    padding-top: 34px;
    display: grid;
    align-items: center;
    grid-template-columns: 1fr 20px;
  }

  &__num {
    color: $background-color;
    font-weight: 500;
    margin-top: 40px;
  }

  &__discription {
    @extend %p-nav__discription;

    &::before {
      position: relative;
      display: block;
      content: " ";
      top: 23px;
      width: 136px;
      height: 1px;
      background-color: $background-color;
    }
  }
}
