.bike-details {

  &__title {
    padding-bottom: 48px;
    text-align: center;

    @media (min-width: 744px) {
      padding-bottom: 64px;
    }

    @media (min-width: 1020px) {
      padding-bottom: 48px;
    }
  }

  &__block {
    display: grid;
    justify-content: center;
  }

  &__image-container {
    display: flex;
    gap: 20px;
    padding-top: 40px;

    @media (min-width: 744px) {
      gap: 24px;
      padding-top: 48px;
    }
  }

  &__function {
    width: 278px;
    padding-top: 16px;

    @media (min-width: 744px) {
      width: 443px;
    }
  }

  &__img {
    height: 130px;
    width: 130px;
    object-fit: cover;

    @media (min-width: 744px) {
      height: 187px;
      width: 187px;
    }

    @media (min-width: 1020px) {
      width: 320px;
      height: 320px;
    }
  }

.wide__image {
  @media (min-width: 744px) {
    height: 187px;
    width: 392px;
  }

  @media (min-width: 1020px) {
    height: 320px;
    width: 672px;
  }
}

  &__discription {
    width: 280px;
    padding-top: 8px;

    @media (min-width: 744px) {
      width: 600px;
    }

    @media (min-width: 1020px) {
      width: 672px;
    }
  }

  &__button-container {
    width: 100%;
    display: flex;
  }

  &__button {
  @extend %button;

  @media (min-width: 744px) {
    width: 324px;
    margin-top: 48px;
  }

  @media (min-width: 1020px) {
    width: 392px;
  }
  }
}
